@import 'src/styles/_colors';

.container {
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}

.header {
  font-size: 2rem;
  font-weight: bold;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem;
}

.custom-stat-dropdowns {
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}

.description {
  white-space: pre-wrap;
  margin-bottom: 0.5rem;
}